import React, { Component } from 'react';
import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      board: [], avail: new Array(9).fill(new Array(9)), selected: { i: -1, j: -1, oldnum: -1 },
      fixedAvail: new Array(9).fill(new Array(9)),
      boardClass: new Array(9),
      boardText: new Array(9).fill(new Array(9)),
      rule: "", tab: "none", fixedBoard: [], remove: false, removed: [],
      canRemove: []
    };
    this.singles = this.singles.bind(this);
    this.subsets = this.subsets.bind(this);
    this.uniques = this.uniques.bind(this);
    this.initializeArrays = this.initializeArrays.bind(this);
    this.reset = this.reset.bind(this);
  };
  componentWillMount() {
    this.initializeArrays();
  }
  initializeArrays() {
    if (this.state.board.length !== 0) this.clear();
    var initboard = new Array(9);
    var nums = [1, 2, 3, 4, 5, 6, 7, 8, 9];
    var num = 0;
    var shuffled = [];
    for (var i = 9; i > 0; i--) {
      num = Math.floor(Math.random() * (i - 1));
      shuffled.push(nums[num]);
      nums.splice(num, 1)
    }
    initboard[0] = [shuffled[0],
    shuffled[1],
    shuffled[2],
    shuffled[3],
    shuffled[4],
    shuffled[5],
    shuffled[6],
    shuffled[7],
    shuffled[8]];
    initboard[1] = [shuffled[3],
    shuffled[4],
    shuffled[5],
    shuffled[6],
    shuffled[7],
    shuffled[8],
    shuffled[0],
    shuffled[1],
    shuffled[2]];
    initboard[2] = [shuffled[6],
    shuffled[7],
    shuffled[8],
    shuffled[0],
    shuffled[1],
    shuffled[2],
    shuffled[3],
    shuffled[4],
    shuffled[5]];
    initboard[3] = [shuffled[1],
    shuffled[2],
    shuffled[3],
    shuffled[4],
    shuffled[5],
    shuffled[6],
    shuffled[7],
    shuffled[8],
    shuffled[0]];
    initboard[4] = [shuffled[4],
    shuffled[5],
    shuffled[6],
    shuffled[7],
    shuffled[8],
    shuffled[0],
    shuffled[1],
    shuffled[2],
    shuffled[3]];
    initboard[5] = [shuffled[7],
    shuffled[8],
    shuffled[0],
    shuffled[1],
    shuffled[2],
    shuffled[3],
    shuffled[4],
    shuffled[5],
    shuffled[6]];
    initboard[6] = [shuffled[2],
    shuffled[3],
    shuffled[4],
    shuffled[5],
    shuffled[6],
    shuffled[7],
    shuffled[8],
    shuffled[0],
    shuffled[1]];
    initboard[7] = [shuffled[5],
    shuffled[6],
    shuffled[7],
    shuffled[8],
    shuffled[0],
    shuffled[1],
    shuffled[2],
    shuffled[3],
    shuffled[4]];
    initboard[8] = [shuffled[8],
    shuffled[0],
    shuffled[1],
    shuffled[2],
    shuffled[3],
    shuffled[4],
    shuffled[5],
    shuffled[6],
    shuffled[7]];
    var available = [];
    for (i = 0; i < 81; i++) {
      available.push(i + 1);
    }

    for (var count = 0; count < 40; count++) {
      num = Math.floor(Math.random() * (80 - count));
      i = Math.floor(available[num] / 9);
      var j = available[num] % 9;
      available.splice(num, 1);
      initboard[i][j] = "";
    }
    let fixedBoard = [];
    initboard.forEach(array => {
      fixedBoard.push([...array]);
    })
    let boardClass = this.state.boardClass;
    let boardText = this.state.boardText;
    for (let i = 0; i < 9; i++) {
      boardText[i] = new Array(9);
      boardClass[i] = new Array(9);
      for (let j = 0; j < 9; j++) {
        boardClass[i][j] = [];
        boardText[i][j] = [];
      }
    }

    this.setState({ board: [...initboard], boardClass, fixedBoard, removed: [] }, () => {
      this.allavail()
    })


  }
  allavail() {
    this.clear(false);
    const avail = []
    for (let i = 0; i < 9; i++) {
      avail.push([])
      for (let j = 0; j < 9; j++) {
        avail[i].push([])
        if (this.state.board[i][j] < 1) {
          const array = [];
          for (let k = 1; k < 10; k++) {
            if (this.checkall(k, i, j)) {
              array.push(k);
            }
          }
          avail[i][j] = array;
        }
      }
    }
    let fixedAvail = [];
    avail.forEach(e => {
      let array = [];
      e.forEach(r => {
        array.push([...r])
      })
      fixedAvail.push(array);
    })
    this.state.removed.forEach(value => {
      if (this.state.board[value.i][value.j] < 1) {
        avail[value.i][value.j] = avail[value.i][value.j].filter(e => {
          return (e * 1 !== 1 * value.value);
        });
      }
    })
    this.setState({ avail, fixedAvail }, () => {
      switch (this.state.rule) {
        case "Uniques": this.uniques(); break;
        case "Singles": this.singles(); break;
        case "Subsets": this.subsets(); break;
        case "Hidden": this.hiddenSubsets(); break;
        default:
      }
    });
  }
  checkrow(input, j) {
    var board = this.state.board;
    for (var i = 0; i < 9; i++) {
      if (board[i][j] === input) return false;
    }
    return true;
  }
  checkcolumn(input, i) {
    var board = this.state.board;
    for (var j = 0; j < 9; j++) {
      if (board[i][j] === input) return false;
    }
    return true;
  }
  checkbox(input, i, j) {
    var boxRight = Math.floor(i / 3)
    var boxDown = Math.floor(j / 3)
    var board = this.state.board;
    for (i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
      for (j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
        if (board[i][j] === input) return false;
      }
    }
    return true;
  }
  checkall(input, i, j) {
    input = 1 * input;
    //this.checkbox(input,i,j)&& this.checkrow(input,j) && this.checkcolumn(input,i)
    return (this.checkbox(input, i, j) && this.checkrow(input, j) && this.checkcolumn(input, i));
  }
  setTab(tab) {
    this.setState({ tab });
  }
  handleKeyPress = (event) => {
    let valid = ["1", "2", "3", "4", "5", "6", "7", "8", "9"];
    let i = this.state.selected.i;
    if (i === -1) return;
    let j = this.state.selected.j
    if (valid.includes(event.key) && this.state.fixedAvail[i][j].includes(event.key * 1)) {
      if (this.state.remove === true) {
        let removed = this.state.removed;
        let temp = removed.filter(e => { return e.i === i && e.j === j && e.value === event.key });
        if (temp.length > 0) {
          removed.splice(removed.indexOf(temp[0]), 1);
          this.removeValid();
          this.setState({ removed }, () => { this.allavail() });
        } else if (this.state.avail[i][j].length > 1) {
          removed.push({ i, j, value: event.key })
          this.removeValid();
          this.setState({ removed }, () => { this.allavail() });
        }
      } else if (this.state.avail[i][j].includes(event.key * 1)) {
        var board = this.state.board;
        board[i][j] = 1 * event.key;
        /* element.classList.remove(...element.classList);
        document.getElementById("cell:" + i + "," + j).classList.remove("selected");
        document.getElementById("availnums").classList.add("fade"); */
        this.setState({ board, selected: { i: -1, j: -1, oldnum: -1 } }, () => { this.allavail() });
      }
    }
  }
  reset() {
    this.clear();
    let board = [];
    this.state.fixedBoard.forEach(array => {
      board.push([...array]);
    })
    this.setState({
      board,
      removed: [],
      rule: ""
    }, () => (this.allavail()));
  }
  click(i, j) {
    if (this.state.fixedBoard[i][j] > 0) {
      return;
    }
    var board = this.state.board;
    if (this.state.selected.i !== -1) {
      /* document.getElementById("cell:" + this.state.selected.i + "," + this.state.selected.j).classList.remove("selected"); */
      board[this.state.selected.i][this.state.selected.j] = this.state.selected.oldnum;
    }
    var newnum = board[i][j];
    board[i][j] = "";
    /* document.getElementById("cell:" + i + "," + j).classList.add("selected"); */
    this.setState({ board: board, selected: { i, j, oldnum: newnum } }, () => (this.allavail()));
    /* if (this.state.fixedAvail[i][j].length > 0) {
      document.getElementById("availnums").classList.remove("fade");
    } else {
      document.getElementById("availnums").classList.add("fade");
    } */
  }
  clear(keepSelected) {
    let boardClass = this.state.boardClass;
    let boardText = this.state.boardText;
    let selected = this.state.selected;
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        boardClass[i][j] = [];
        boardText[i][j] = "";
        /* let element = document.getElementById("cell:" + i + "," + j);
        element.classList.remove(...element.classList);
        element.innerText = this.state.board[i][j]; */
      }
    }
    if (keepSelected !== false && this.state.selected.i !== -1) {
      boardText[selected.i][selected.j] = selected.oldnum;
      selected = { i: -1, j: -1, oldnum: -1 };

      /* document.getElementById("cell:" + this.state.selected.i + "," + this.state.selected.j).classList.remove("selected");
      let board = this.state.board;
      board[this.state.selected.i][this.state.selected.j] = this.state.selected.oldnum;
      this.setState({ board, selected: { i: -1, j: -1, oldnum: -1 } });
      document.getElementById("availnums").classList.add("fade"); */
    }
    this.setState({ boardClass, boardText, selected });
  }
  setRule(rule) {
    this.clear();
    switch (rule) {
      case "Singles":
        this.singles();
        break;
      case "Uniques":
        this.uniques();
        break;
      case "Subsets":
        this.subsets();
        break;
      case "Hidden":
        this.hiddenSubsets();
        break;
      default:
    }
    this.setState({ rule });
  }

  singles() {
    let boardClass = this.state.boardClass
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        if (this.state.avail[i][j].length === 1) {
          boardClass[i][j].push("single");
        }
      }
    }
    this.setState({ boardClass });
  }
  hiddenSubsets2helper(k, numbers, boardClassText) {
    let boardClass = boardClassText.boardClass;
    let boardText = boardClassText.boardText;
    let canRemove = [];
    let skip = false;
    if (numbers[k].length > 1 && numbers[k].length < 3) {
      for (let l = k + 1; l < 10; l++) {
        if (numbers[l].length > 1 && numbers[l].length < 3) {
          let temp = this.sumArray(numbers[k], numbers[l]);
          if (temp.length === 2) {
            this.state.avail[temp[0].i][temp[0].j].forEach((e)=>{
              if(e !== k && e !== l) canRemove.push({i:temp[0].i,j:temp[0].j, value:e})
            })
            this.state.avail[temp[1].i][temp[1].j].forEach((e)=>{
              if(e !== k && e !== l) canRemove.push({i:temp[1].i,j:temp[1].j, value:e})
            })

            boardClass[temp[0].i][temp[0].j].push("hiddensubset2")
            boardText[temp[0].i][temp[0].j] = this.state.avail[temp[0].i][temp[0].j].map((entry,index)=>{
              return <span className={(canRemove.filter((e)=>(e.i === temp[0].i && e.j === temp[0].j && e*1 === 1*entry)).length > 0 ? "text-danger":"")}>{entry}</span>
            });
            boardClass[temp[1].i][temp[1].j].push("hiddensubset2")
            boardText[temp[1].i][temp[1].j] = this.state.avail[temp[1].i][temp[1].j].map((entry,index)=>(
              <span className={(canRemove.filter((e)=>(e.i === temp[1].i && e.j === temp[1].j && e*1 === 1*entry)).length > 0 ? "text-danger":"")}>{entry}</span>
            ));            
            

            skip = true; break;
          }
        }
      }
      if (skip === true) return { boardClass, boardText };
    }
    return { boardClass, boardText, canRemove }
  }
  hiddenSubsets3helper(k, numbers, boardClassText) {
    let boardClass = boardClassText.boardClass;
    let boardText = boardClassText.boardText;
    if (numbers[k].length > 1 && numbers[k].length < 4) {
      for (let l = k + 1; l < 10; l++) {
        if (numbers[l].length > 1 && numbers[l].length < 4) {
          let temp = this.sumArray(numbers[k], numbers[l]);
          if (temp.length < 4) {
            for (let m = l + 1; m < 10; m++) {
              if (numbers[m].length > 1 && numbers[m].length < 4) {
                let temp = this.sumArray(numbers[k], numbers[l], numbers[m]);
                if (temp.length === 3) {
                  boardClass[temp[0].i][temp[0].j].push("hiddensubset3")
                  boardText[temp[0].i][temp[0].j] = [k, l, m].join(",");
                  boardClass[temp[1].i][temp[1].j].push("hiddensubset3")
                  boardText[temp[1].i][temp[1].j] = [k, l, m].join(",");
                  boardClass[temp[2].i][temp[2].j].push("hiddensubset3")
                  boardText[temp[2].i][temp[2].j] = [k, l, m].join(",");
                }
              }
            }
          }
        }
      }
    }
    return { boardClass, boardText }
  }
  hiddenSubsets() {
    let boardClassText = { boardClass: this.state.boardClass, boardText: this.state.boardText, canRemove: this.state.canRemove }
    for (let i = 0; i < 9; i++) {
      let numbers = new Array(10);
      for (let k = 0; k < 10; k++) {
        numbers[k] = [];
      }
      for (let j = 0; j < 9; j++) {
        let temp = this.state.avail[i][j];
        for (let k = 0; k < temp.length; k++) {
          numbers[temp[k]].push({ i: i, j: j })
        }
      }
      for (let k = 1; k < 10; k++) {
        boardClassText = this.hiddenSubsets2helper(k, numbers, boardClassText);
        boardClassText = this.hiddenSubsets3helper(k, numbers, boardClassText);
      }
      for (let j = 0; j < 9; j++) {
        let numbers = new Array(10);
        for (let k = 0; k < 10; k++) {
          numbers[k] = [];
        }
        for (let i = 0; i < 9; i++) {
          let temp = this.state.avail[i][j];
          for (let k = 0; k < temp.length; k++) {
            numbers[temp[k]].push({ i: i, j: j })
          }
        }
        for (let k = 1; k < 10; k++) {
          boardClassText = this.hiddenSubsets2helper(k, numbers, boardClassText);
          boardClassText = this.hiddenSubsets3helper(k, numbers, boardClassText);
        }
        for (let boxRight = 0; boxRight < 3; boxRight++) {
          for (let boxDown = 0; boxDown < 3; boxDown++) {
            let numbers = new Array(10);
            for (let k = 0; k < 10; k++) {
              numbers[k] = [];
            }
            for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
              for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
                let temp = this.state.avail[i][j];
                for (let k = 0; k < temp.length; k++) {
                  numbers[temp[k]].push({ i: i, j: j })
                }
              }
            }
            for (let k = 1; k < 10; k++) {
              boardClassText = this.hiddenSubsets2helper(k, numbers, boardClassText);
              boardClassText = this.hiddenSubsets3helper(k, numbers, boardClassText);
            }
          }
        }
      }
    }
    this.setState({ boardClass:boardClassText.boardClass, boardText:boardClassText.boardText, canRemove:boardClassText.canRemove });
  }
  sumArray(a, ...args) {
    let temp = [...a]
    args.forEach(arg => {
      arg.forEach(element => {
        if (temp.filter(e => { return (e.i === element.i && e.j === element.j) }).length === 0) temp.push(element);
      });
    })
    return temp
  }

  removeValid() {
    this.setState({ remove: !this.state.remove })
  }
  compare(a, b) {
    return JSON.stringify(a) === JSON.stringify(b)
  }
  secondvalues(a, b) {
    if (b.length < 2) return false;
    if (a.length === 2) {
      if (this.compare(a, b)) return false;
    }
    let temp = [...a];
    b.forEach(element => {
      if (!temp.includes(element)) temp.push(element);
    });
    return temp.length < 4;
  }
  thirdvalue(a, b, c) {
    if (c.length < 2) return false;
    if (a.length === 2) {
      if (this.compare(a, b) || this.compare(a, c)) return false
    }
    if (b.length === 2) {
      if (this.compare(b, c)) return false
    }
    let temp = [...a];
    b.forEach(element => {
      if (!temp.includes(element)) temp.push(element);
    });
    c.forEach(element => {
      if (!temp.includes(element)) temp.push(element);
    });
    return temp.length < 4;
  }
  subsets() {
    let boardClass = this.state.boardClass;
    let boardText = this.state.boardText;
    for (let i = 0; i < 9; i++) {
      for (let j = 0; j < 9; j++) {
        if (this.state.board[i][j] < 1 && this.state.avail[i][j].length < 4 && this.state.avail[i][j].length > 1) {
          let array = this.state.avail[i][j];
          let secondvalues = [];
          let ib = i;
          for (let jb = j + 1; jb < 9; jb++) {
            if (this.state.board[ib][jb] < 1 && this.secondvalues(array, this.state.avail[ib][jb])) {
              secondvalues.forEach(value => {
                if (this.thirdvalue(array, value.avail, this.state.avail[ib][jb])) {
                  boardClass[i][j].push("subset3");
                  boardText[i][j] = array.join(",");
                  boardClass[value.i][value.j].push("subset3");
                  boardText[value.i][value.j] = value.avail.join(",");
                  boardClass[ib][jb].push("subset3");
                  boardText[ib][jb] = this.state.avail[ib][jb].join(",");

                  /*  document.getElementById("cell:" + i + "," + j).classList.add("subset3");
                   document.getElementById("cell:" + i + "," + j).innerText = array.join(",");
                   document.getElementById("cell:" + value.i + "," + value.j).classList.add("subset3");
                   document.getElementById("cell:" + value.i + "," + value.j).innerText = value.avail.join(",");
                   document.getElementById("cell:" + ib + "," + jb).classList.add("subset3");
                   document.getElementById("cell:" + ib + "," + jb).innerText = this.state.avail[ib][jb].join(","); */
                }

              })
              secondvalues.push({ i: ib, j: jb, avail: this.state.avail[ib][jb] });
            }
          }
        }
      }
    }
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        if (this.state.board[i][j] < 1 && this.state.avail[i][j].length < 4 && this.state.avail[i][j].length > 1) {
          let array = this.state.avail[i][j];
          let secondvalues = [];
          let jb = j;
          for (let ib = i + 1; ib < 9; ib++) {
            if (this.state.board[ib][jb] < 1 && this.secondvalues(array, this.state.avail[ib][jb])) {
              secondvalues.forEach(value => {
                if (this.thirdvalue(array, value.avail, this.state.avail[ib][jb])) {
                  boardClass[i][j].push("subset3");
                  boardText[i][j] = array.join(",");
                  boardClass[value.i][value.j].push("subset3");
                  boardText[value.i][value.j] = value.avail.join(",");
                  boardClass[ib][jb].push("subset3");
                  boardText[ib][jb] = this.state.avail[ib][jb].join(",");

                  /*  document.getElementById("cell:" + i + "," + j).classList.add("subset3");
                   document.getElementById("cell:" + i + "," + j).innerText = array.join(",");
                   document.getElementById("cell:" + value.i + "," + value.j).classList.add("subset3");
                   document.getElementById("cell:" + value.i + "," + value.j).innerText = value.avail.join(",");
                   document.getElementById("cell:" + ib + "," + jb).classList.add("subset3");
                   document.getElementById("cell:" + ib + "," + jb).innerText = this.state.avail[ib][jb].join(","); */
                }
              })
              secondvalues.push({ i: ib, j: jb, avail: this.state.avail[ib][jb] });
            }
          }
        }
      }
    }
    for (let boxRight = 0; boxRight < 3; boxRight++) {
      for (let boxDown = 0; boxDown < 3; boxDown++) {
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
            if (this.state.board[i][j] < 1 && this.state.avail[i][j].length < 4 && this.state.avail[i][j].length > 1) {
              let array = this.state.avail[i][j];
              let secondvalues = [];
              for (let ib = boxRight * 3; ib < (boxRight + 1) * 3; ib++) {
                for (let jb = boxDown * 3; jb < (boxDown + 1) * 3; jb++) {
                  if (this.state.board[ib][jb] < 1 && (ib !== i || jb !== j) && this.secondvalues(array, this.state.avail[ib][jb])) {
                    secondvalues.forEach(value => {
                      if (this.thirdvalue(array, value.avail, this.state.avail[ib][jb])) {
                        boardClass[i][j].push("subset3");
                        boardText[i][j] = array.join(",");
                        boardClass[value.i][value.j].push("subset3");
                        boardText[value.i][value.j] = value.avail.join(",");
                        boardClass[ib][jb].push("subset3");
                        boardText[ib][jb] = this.state.avail[ib][jb].join(",");

                        /* document.getElementById("cell:" + i + "," + j).classList.add("subset3");
                        document.getElementById("cell:" + i + "," + j).innerText = array.join(",");
                        document.getElementById("cell:" + value.i + "," + value.j).classList.add("subset3");
                        document.getElementById("cell:" + value.i + "," + value.j).innerText = value.avail.join(",");
                        document.getElementById("cell:" + ib + "," + jb).classList.add("subset3");
                        document.getElementById("cell:" + ib + "," + jb).innerText = this.state.avail[ib][jb].join(","); */
                      }
                    })
                    secondvalues.push({ i: ib, j: jb, avail: this.state.avail[ib][jb] });
                  }
                }
              }
            }
          }
        }
      }
    }



    for (let i = 0; i < 9; i++) {
      for (let j = 0; j < 9; j++) {
        let ib = i;
        if (this.state.board[i][j] < 1 && this.state.avail[i][j].length === 2) {
          for (let jb = j + 1; jb < 9; jb++) {
            if (this.state.board[ib][jb] < 1) {
              if (this.compare(this.state.avail[ib][jb], this.state.avail[i][j])) {
                boardClass[i][j].push("subset2");
                boardText[i][j] = this.state.avail[i][j].join(",");
                boardClass[ib][jb].push("subset2");
                boardText[ib][jb] = this.state.avail[i][j].join(",");
                /* document.getElementById("cell:" + i + "," + j).classList.add("subset2");
                document.getElementById("cell:" + i + "," + j).innerText = this.state.avail[i][j].join(",");
                document.getElementById("cell:" + ib + "," + jb).classList.add("subset2");
                document.getElementById("cell:" + ib + "," + jb).innerText = this.state.avail[ib][jb].join(","); */
              }
            }
          }
        }
      }
    }
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        let jb = j;
        if (this.state.board[i][j] < 1 && this.state.avail[i][j].length === 2) {
          for (let ib = i + 1; ib < 9; ib++) {
            if (this.state.board[ib][jb] < 1) {
              if (this.compare(this.state.avail[ib][jb], this.state.avail[i][j])) {
                boardClass[i][j].push("subset2");
                boardText[i][j] = this.state.avail[i][j].join(",");
                boardClass[ib][jb].push("subset2");
                boardText[ib][jb] = this.state.avail[i][j].join(",");
                /*  document.getElementById("cell:" + i + "," + j).classList.add("subset2");
                 document.getElementById("cell:" + i + "," + j).innerText = this.state.avail[i][j].join(",");
                 document.getElementById("cell:" + ib + "," + jb).classList.add("subset2");
                 document.getElementById("cell:" + ib + "," + jb).innerText = this.state.avail[ib][jb].join(","); */
              }
            }
          }
        }
      }
    }
    for (let boxRight = 0; boxRight < 3; boxRight++) {
      for (let boxDown = 0; boxDown < 3; boxDown++) {
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          for (let j = boxRight * 3; j < (boxRight + 1) * 3; j++) {
            if (this.state.board[i][j] < 1 && this.state.avail[i][j].length === 2) {
              for (let ib = boxRight * 3; ib < (boxRight + 1) * 3; ib++) {
                for (let jb = boxRight * 3; jb < (boxRight + 1) * 3; jb++) {
                  if (this.state.board[ib][jb] < 1) {
                    if ((i !== ib || j !== jb) && this.compare(this.state.avail[ib][jb], this.state.avail[i][j])) {
                      boardClass[i][j].push("subset2");
                      boardText[i][j] = this.state.avail[i][j].join(",");
                      boardClass[ib][jb].push("subset2");
                      boardText[ib][jb] = this.state.avail[i][j].join(",");
                      /* document.getElementById("cell:" + i + "," + j).classList.add("subset2");
                      document.getElementById("cell:" + i + "," + j).innerText = this.state.avail[i][j].join(",");
                      document.getElementById("cell:" + ib + "," + jb).classList.add("subset2");
                      document.getElementById("cell:" + ib + "," + jb).innerText = this.state.avail[ib][jb].join(","); */
                    }
                  }

                }
              }
            }
          }
        }
      }
    }
    this.setState({ boardClass, boardText });
  }
  uniques() {
    let boardText = this.state.boardText;
    let boardClass = this.state.boardClass;
    for (let k = 1; k < 10; k++) {
      let pos = { i: 0, j: 0 };
      for (let j = 0; j < 9; j++) {
        let count = 0;
        for (let i = 0; i < 9; i++) {
          if (this.state.avail[i][j].includes(k)) {
            pos.i = i;
            pos.j = j;
            count++;
          }
          if (count > 1) break;
        }
        if (count === 1 && this.state.avail[pos.i][pos.j].length !== 1) {
          boardClass[pos.i][pos.j].push("unique");
          boardText[pos.i][pos.j] = k;
          /* document.getElementById("cell:" + pos.i + "," + pos.j).classList.add("unique");
          document.getElementById("cell:" + pos.i + "," + pos.j).innerText = k; */
        }
      }
      for (let i = 0; i < 9; i++) {
        let count = 0;
        for (let j = 0; j < 9; j++) {
          if (this.state.avail[i][j].includes(k)) {
            pos.i = i;
            pos.j = j;
            count++;
          }
          if (count > 1) break;
        }
        if (count === 1 && this.state.avail[pos.i][pos.j].length !== 1) {
          boardClass[pos.i][pos.j].push("unique");
          boardText[pos.i][pos.j] = k;
          /* document.getElementById("cell:" + pos.i + "," + pos.j).classList.add("unique");
          document.getElementById("cell:" + pos.i + "," + pos.j).innerText = k; */
        }
      }
      for (let boxRight = 0; boxRight < 3; boxRight++) {
        for (let boxDown = 0; boxDown < 3; boxDown++) {
          let count = 0;
          for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
            for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
              if (this.state.avail[i][j].includes(k)) {
                count++;
                pos.i = i;
                pos.j = j;
              }
              if (count > 1) break;
            }
            if (count > 1) break;
          }
          if (count === 1 && this.state.avail[pos.i][pos.j].length !== 1) {
            boardClass[pos.i][pos.j].push("unique");
            boardText[pos.i][pos.j] = k;
            /* document.getElementById("cell:" + pos.i + "," + pos.j).classList.add("unique");
            document.getElementById("cell:" + pos.i + "," + pos.j).innerText = k */;
          }
        }
      }
    }
    this.setState({ boardClass, boardText });
  }
  render() {
    const board = [];
    let availableNumbers = [];
    let columnNumbers = [];
    let rowNumbers = [];
    let boxNumbers = [];
    let boardClass = this.state.boardClass;
    let boardText = this.state.boardText;

    for (let j = 0; j < 9; j++) {
      const row = [];
      for (let i = 0; i < 9; i++) {
        let cls = [...boardClass[i][j]];
        let txt = boardText[i][j];
        if (this.state.selected.i === i && this.state.selected.j === j) {
          cls = ["selected"];
          txt = ""
        }
        if (this.state.fixedBoard[i][j] > 0) {

        } else if (this.state.board[i][j] > 0) {
          cls.push("text-primary")
        }
        row.push(<div key={"cell:" + i + "," + j} id={"cell:" + i + "," + j} className={cls.join(" ")} onClick={() => this.click(i, j)}
          style={{
            "verticalAlign": "top",
            "textAlign": "center",
            "borderRightWidth": (((i % 3) === 2) ? 3 : 0) + 1 + "px",
            "borderLeftWidth": ((i === 0) ? 3 : 0) + 1 + "px",
            "borderBottomWidth": (((j % 3) === 2) ? 3 : 0) + 1 + "px",
            "borderTopWidth": ((j === 0) ? 3 : 0) + 1 + "px",
            "borderStyle": "solid", height: 20 + "px", width: 20 + "px", display: "inline-block", borderColor: "black"
          }}>{(this.state.board[i][j] > 0) ? this.state.board[i][j] : txt}</div>);
      }
      board.push(<div key={"row:" + j}>{row}</div>);
    }
    if (this.state.selected.i !== -1) {
      let i = this.state.selected.i;
      let j = this.state.selected.j;
      let removed = this.state.removed.filter(r => { return r.i === i && r.j === j })
      let first = true;
      this.state.fixedAvail[i][j].forEach(e => {
        if (!first) availableNumbers.push(<span>,</span>);
        first = false;
        if (removed.filter(r => { return r.value * 1 === 1 * e }).length > 0) {
          availableNumbers.push(<span className="removedNumber">{e}</span>);

        } else {
          availableNumbers.push(<span>{e}</span>);
        }
      })
      for (let j = 0; j < 9; j++) {
        let numbers = "";

        if (this.state.board[i][j]) {
          numbers = "Set to " + this.state.board[i][j];
        } else {
          numbers = this.state.avail[i][j].toString();
        }
        numbers = <div>{numbers}</div>;
        columnNumbers.push(numbers);
      }
      for (let i = 0; i < 9; i++) {
        let numbers = "";
        if (this.state.board[i][j]) {
          numbers = "Set to " + this.state.board[i][j];
        } else {
          numbers = this.state.avail[i][j].toString();
        }
        numbers = <div>{numbers}</div>;
        rowNumbers.push(numbers);
      }
      var boxRight = Math.floor(i / 3)
      var boxDown = Math.floor(j / 3)
      for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          let numbers = "";
          if (this.state.board[i][j]) {
            numbers = "Set to " + this.state.board[i][j];
          } else {
            numbers = this.state.avail[i][j].toString();
          }
          numbers = <div>{numbers}</div>;
          boxNumbers.push(numbers);
        }
      }
    } else {
      availableNumbers = [];
      columnNumbers = [];
      rowNumbers = [];
      boxNumbers = [];
    }
    columnNumbers = <div>{columnNumbers}</div>;

    return (
      <div tabIndex="0" onKeyPress={this.handleKeyPress}>
        <div className="d-flex justify-content-center">
          <div>
            <div className="board">{board}</div>
            <div>
              <button id="singlesBtn" onClick={() => { this.setRule("Singles") }} className={"btn text-white highlightBtns " + (this.state.rule !== "Singles" ? "bg-primary" : "bg-secondary")}>Singles</button>
              <button id="uniquesBtn" onClick={() => { this.setRule("Uniques") }} className={"btn text-white highlightBtns " + (this.state.rule !== "Uniques" ? "bg-primary" : "bg-secondary")}>Uniques</button>
              <button id="subsetsBtn" onClick={() => { this.setRule("Subsets") }} className={"btn text-white highlightBtns " + (this.state.rule !== "Subsets" ? "bg-primary" : "bg-secondary")}>Subsets</button>
              <button id="hiddenBtn" onClick={() => { this.setRule("Hidden") }} className={"btn text-white highlightBtns " + (this.state.rule !== "Hidden" ? "bg-primary" : "bg-secondary")}>Hidden</button>
            </div>
            <div className="d-flex justify-content-center mt-2">
              <button onClick={this.reset} className="btn bg-primary text-white mr-2" >Reset</button>
              <button onClick={() => { this.reset(); this.initializeArrays() }} className="btn bg-primary text-white mr-2" >New Game</button>
            </div>
          </div>
        </div>
        <div className="d-flex justify-content-center">
          <div id="availnums" className={"d-flex flex-column " + ((this.state.selected.i !== -1) ? "" : "fade")}>
            <div className="avail pr-2">
              <div className="availnums pb-2"><div>Valid numbers for selected cell</div>
                {(this.state.selected.i !== -1) && this.state.fixedAvail[this.state.selected.i][this.state.selected.j].map((number, index) => (
                  <button onClick={() => { this.handleKeyPress({ key: "" + number }) }}
                    className={
                      "btn-sm " + ((this.state.removed.filter((e) => (e.i === this.state.selected.i && e.j === this.state.selected.j && e.value * 1 === 1 * number)).length > 0) ? "text-danger" : "")}>{number}</button>
                ))}


              </div>
              <button id="removeButton" onClick={this.removeValid.bind(this)} className={"btn-small text-white  " + (this.state.remove ? "bg-danger" : "bg-primary")} >Remove</button>
            </div>
            <div className="mt-2">
              <button onClick={() => { this.setTab("Row") }} className={"btn text-white mr-2 " + (this.state.tab === "Row" ? "bg-secondary" : "bg-primary")}>Row</button>
              <button onClick={() => { this.setTab("Column") }} className={"btn text-white mr-2 " + (this.state.tab === "Column" ? "bg-secondary" : "bg-primary")}>Column</button>
              <button onClick={() => { this.setTab("Box") }} className={"btn text-white mr-2 " + (this.state.tab === "Box" ? "bg-secondary" : "bg-primary")}>Box</button>
            </div>
            <div className="avail">
              <div id="row" className={"availbox " + (this.state.tab === "Row" ? "" : "none")}>
                <div>Valid numbers for cells in row</div>
                {rowNumbers}
              </div>
              <div id="column" className={"availbox " + (this.state.tab === "Column" ? "" : "none")}><div>Valid numbers for cells in column</div>
                {columnNumbers}
              </div>
              <div id="box" className={"availbox " + (this.state.tab === "Box" ? "" : "none")}>
                <div>Valid numbers for cells in box</div>
                {boxNumbers}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };
}
export default App;
